import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.scss';
import BookList from './component/BookList';
import PDFviewer from './component/PDFviewer';
import AllMemo from './component/AllMemo';

const App = () => {

  const HomeImg = require("./img/book.png");

  return (
      <div id="bigWrapper">
          <Switch>
            <Route exact="true" path={process.env.PUBLIC_URL + "/"} component={BookList} />
            <Route path={process.env.PUBLIC_URL + "/book-name=:bookName"} component={PDFviewer} />
          </Switch>
          <Switch>
            <Route exact="true" path={process.env.PUBLIC_URL + "/"} component={AllMemo} />
            <Route path={process.env.PUBLIC_URL + "/book-name=:bookName"} component={AllMemo} />
          </Switch>
        <a  className="goToHome" href="/DongSeoNamBOOK">
          <img src={HomeImg} alt="HOME" />
        </a>
      </div>
  );
}

export default App;
